import React from 'react'
import { connect } from 'react-redux'
import { fetchUserData } from '../store'

const TestRemove = ({ data }) => {
    return (
        <div>
            {data}
        </div>
    )
}

TestRemove.getInitialProps = async ({ reduxStore, state }) => {
    let res = await reduxStore.dispatch(fetchUserData())
    return {
        data: "random"
    }
}

export default connect()(TestRemove)